@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&family=Mate+SC&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bodoni+Moda:ital,wght@1,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  padding: 0;
  margin: 0;
  transition-duration: 0.8s;
}

.title {
  font-family: "Mate SC", serif;
}

body {
  background-color: #ffffff;
  /* background-image: url("./background.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  background-color: #121212; */
}

.app {
  color: rgb(0, 0, 0);
}

.app__search_container {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-justify-content: center;
          justify-content: center;
}

.about_application_content {
  padding: 25px;
  max-width: 650px;
  margin: 0px 10px 40px 10px;
  text-align: justify;
  border: 2px solid black;
  border-radius: 6px;
  box-shadow: 0 0 8px gray;
  font-size: 20px;
  font-family: "Bodoni Moda", serif;
}

.search_wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-content: space-around;
          align-content: space-around;
  width: 500px;
  padding: 0;
  margin-left: 10px;
  margin-right: 10px;
  padding: 10px;
}

.search_input {
  margin-top: 10px !important;
  font-family: "Mate SC", serif;
  background-color: whitesmoke !important;
  padding: 15px;
  border: none;
  font-size: 22px;
  text-align: center;
  transition: 0.3s;
  font-weight: bolder;
}

.search_input:hover {
  box-shadow: 0 0 8px rgb(104, 116, 255);
}

.search_button {
  font-size: 18px !important;
  margin-top: 10px !important;
}

.suggestion_container {
  z-index: 10;
  margin-top: -53px;
}

.suggestion {
  background-color: white;
  color: black;
  font-family: "Bodoni Moda", serif;
  border: 1px solid black;
  font-weight: bolder;
  border-radius: 2px;
  padding: 8px;
  transition-duration: 0.6s;
  text-align: center;
}

.suggestion:hover {
  cursor: pointer;
  background-color: rgb(219, 221, 221);
}

.recommendation_row {
  display: -webkit-flex;
  display: flex;
  overflow-y: hidden;
  overflow-x: scroll;
  padding: 20px 20px 20px 0;
  margin-left: 20px;
  margin-right: 20px;
}

.recommendation_row::-webkit-scrollbar {
  width: 1em;
}

.recommendation_row::-webkit-scrollbar-track {
  border-radius: 10px;
}

.recommendation_row::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: grey;
}

.recommendation_row::-webkit-scrollbar:vertical {
  display: none;
}

.input_movie_container {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.input_movie_card {
  display: -webkit-flex;
  display: flex;
  margin: 20px;
  max-width: 900px;
  box-shadow: 0 0 10px gray;
  border: 2px solid black;
  border-radius: 10px;
  background-color: #424242;
  font-family: "Bodoni Moda", serif;
}

.input_poster {
  width: 45%;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

.card_content {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-content: space-between;
          align-content: space-between;
  padding-left: 15px;
  padding-top: 15px;
  padding-right: 8px;
  padding-bottom: 15px;
  color: rgba(255, 255, 255, 0.7);
}

.card_overview {
  text-align: justify;
}

.card_title {
  color: rgb(255, 0, 0);
}


.cast_data_cards {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.reviews_container {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  background-color: #121212 !important;
  -webkit-justify-content: center;
          justify-content: center;
}

.review_content_container {
  width: 100%;
  height: 60px;
  overflow-y: scroll;
}

.review_content_container::-webkit-scrollbar {
  display: none;
}

.table__row_content {
  color: #fff !important;
  text-align: justify !important;
  font-family: "Bodoni Moda", serif;
  font-size: 16px;
}

@media only screen and (max-width: 600px) {
  .input_movie_card {
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .input_poster {
    width: 100%;
    height: 450px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    border-bottom-left-radius: 0px;
  }
}

.about_title {
  font-size: 40px;
}

.about_name {
  font-family: "Dancing Script", cursive;
  font-size: 36px;
  text-align: center;
}

.aboutme_container {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-justify-content: center;
          justify-content: center;
}

.aboutme_card {
  display: -webkit-flex;
  display: flex;
  max-width: 800px;
  margin: 10px 10px 20px 10px;
  box-shadow: 0 0 6px whitesmoke;
  border: 2px solid black;
  border-radius: 5px;
  transition-duration: 0.4s;
}

.aboutme_card:hover {
  box-shadow: 0 0 10px blue;
}

.aboutme_card__image {
  width: 35%;
}

.aboutme_card__content {
  padding: 25px 15px 15px 15px;
  text-align: justify;
  font-family: "Bodoni Moda", serif;
}

.aboutme_card__sociallinks {
  display: -webkit-flex;
  display: flex;
  margin-top: 20px;
  transition-delay: 0.4s;
}

.social_icon_linkedin {
  font-size: 25px;
  margin-right: 10px;
  margin-top: 5px;
  background-color: rgb(0, 0, 0, 0.9);
  color: #0077b5;
  padding: 10px;
  padding-right: 10px;
  cursor: pointer;
  transition: all 0.2s ease-in-out !important;
}

.social_icon_github {
  font-size: 25px;
  margin-top: 5px;
  margin-left: 10px;
  background-color: rgb(0, 0, 0, 0.9);
  color: #6cc644;
  padding: 10px;
  padding-left: 10px;
  cursor: pointer;
  transition: all 0.2s ease-in-out !important;
}

.social_icon_linkedin:hover {
  background-color: #fff;
  border-radius: 50%;
}

.social_icon_github:hover {
  background-color: #fff;
  border-radius: 50%;
  color: green;
}

@media only screen and (max-width: 600px) {
  .aboutme_card {
    -webkit-flex-direction: column;
            flex-direction: column;
  }

  .aboutme_card__image {
    width: 100%;
  }

  .aboutme_card__content {
    text-align: center;
  }

  .aboutme_card__sociallinks {
    -webkit-justify-content: center;
            justify-content: center;
  }
}

.title_card_wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%;
}

.title_card {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: center;
          align-items: center;
  padding: 25px;
  margin: 18px 10px 30px 10px;
  width: 550px;
  box-shadow: 0 0 10px red;
  border-radius: 8px;
  font-size: 20px;
  text-align: center;
  transition: 0.3s;
}

.title_logo {
  width: 15%;
}

@media only screen and (max-width: 600px) {
  .title_card {
    font-size: 16px;
    width: 100%;
  }

  .title_logo {
    width: 22%;
  }
}

